  font-size: 24px;
}

.country-info {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.card {
  width: 400px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  padding: 20px;
  transition: all .3s ease-in-out;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, .1);
}

